<template>
    <div>
        <b-col class="play-list">
            <h2>Play List</h2>
            <MovieItem v-for="movie in movieList" :key="movie.cover" :item="movie"></MovieItem>
        </b-col>
    </div>
</template>

<script>

import MovieItem from '../MovieItem.vue'
import DATA from '../../assets/data'

let movie = DATA[0]

if (location.search) {
  var vid = location.search.split('=')[1]
  DATA.forEach(item => {
    if (item.id === vid) {
      movie = item
    }
  })
}


export default {
  name: 'Main',
  components: {
      MovieItem
  },
  data() {
    return {
      title: '',
      movieList: DATA,
      currentMovie: movie
    }
  },
  methods: {
      
  },
}
</script>
